"use client";
import React, { useState } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Table, TableBody, TableCell, TableHeader, TableRow } from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Modal } from "@/components/ui/modal";
import Image from "next/image";

type TicketStatus = "Draft" | "Submitted" | "In Progress" | "Resolved" | "Rejected";
type TicketPriority = "Low" | "Medium" | "High";
type TicketCategory = "IT" | "HR" | "Facilities" | "Other";

interface User {
    name: string;
    avatar?: string;
}

interface Ticket {
    id: number;
    subject: string;
    description: string;
    category: TicketCategory;
    priority: TicketPriority;
    status: TicketStatus;
    attachments: File[];
    createdBy: User;
    createdAt: string;
    activity: { status: TicketStatus; timestamp: string }[];
}

// Dummy initial tickets
const initialTickets: Ticket[] = [
    {
        id: 1,
        subject: "Laptop not working",
        description: "My laptop is not booting properly.",
        category: "IT",
        priority: "High",
        status: "Draft",
        attachments: [],
        createdBy: { name: "John Doe", avatar: "/images/user/user-17.jpg" },
        createdAt: "2026-01-01",
        activity: [{ status: "Draft", timestamp: "2026-01-01 10:00" }],
    },
    {
        id: 2,
        subject: "Request for HR document",
        description: "Need copy of payslip for last month.",
        category: "HR",
        priority: "Medium",
        status: "Submitted",
        attachments: [],
        createdBy: { name: "Alice Smith", avatar: "/images/user/user-18.jpg" },
        createdAt: "2026-01-02",
        activity: [
            { status: "Draft", timestamp: "2026-01-02 09:00" },
            { status: "Submitted", timestamp: "2026-01-02 09:30" },
        ],
    },
];

export default function HelpDeskPage() {
    const [tickets, setTickets] = useState<Ticket[]>(initialTickets);
    const [modalOpen, setModalOpen] = useState(false);
    const [editingTicket, setEditingTicket] = useState<Ticket | null>(null);

    // Form states
    const [subject, setSubject] = useState("");
    const [description, setDescription] = useState("");
    const [category, setCategory] = useState<TicketCategory>("IT");
    const [priority, setPriority] = useState<TicketPriority>("Low");
    const [attachments, setAttachments] = useState<File[]>([]);

    // Filters
    const [filterStatus, setFilterStatus] = useState<TicketStatus | "All">("All");
    const [filterPriority, setFilterPriority] = useState<TicketPriority | "All">("All");

    // Pagination
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 5;

    const openNewTicket = () => {
        setEditingTicket(null);
        setSubject("");
        setDescription("");
        setCategory("IT");
        setPriority("Low");
        setAttachments([]);
        setModalOpen(true);
    };

    const openTicket = (ticket: Ticket) => {
        setEditingTicket(ticket);
        setSubject(ticket.subject);
        setDescription(ticket.description);
        setCategory(ticket.category);
        setPriority(ticket.priority);
        setAttachments(ticket.attachments);
        setModalOpen(true);
    };

    const closeModal = () => setModalOpen(false);

    const handleAttachmentChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (!e.target.files) return;
        setAttachments(Array.from(e.target.files));
    };

    const handleSubmitTicket = () => {
        if (!subject || !description) return alert("Please fill subject and description.");

        const newTicket: Ticket = {
            id: editingTicket ? editingTicket.id : tickets.length + 1,
            subject,
            description,
            category,
            priority,
            status: "Submitted",
            attachments,
            createdBy: editingTicket?.createdBy || { name: "Current User" },
            createdAt: editingTicket?.createdAt || new Date().toISOString(),
            activity: editingTicket
                ? [...editingTicket.activity, { status: "Submitted", timestamp: new Date().toISOString() }]
                : [{ status: "Submitted", timestamp: new Date().toISOString() }],
        };

        if (editingTicket) {
            setTickets(tickets.map((t) => (t.id === editingTicket.id ? newTicket : t)));
        } else {
            setTickets([newTicket, ...tickets]);
        }

        setModalOpen(false);
    };

    // Filters + pagination
    const filteredTickets = tickets.filter(
        (t) =>
            (filterStatus === "All" || t.status === filterStatus) &&
            (filterPriority === "All" || t.priority === filterPriority)
    );
    const totalPages = Math.ceil(filteredTickets.length / itemsPerPage);
    const displayedTickets = filteredTickets.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );

    const openAttachment = (file: File) => window.open(URL.createObjectURL(file));

    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Help Desk" />

            <div className="flex justify-between mb-4">
                <h2 className="text-xl font-semibold">Tickets</h2>
                <button
                    className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                    onClick={openNewTicket}
                >
                    New Ticket
                </button>
            </div>

            {/* Filters */}
            <div className="flex gap-4 mb-4">
                <select
                    className="border rounded px-3 py-2"
                    value={filterStatus}
                    onChange={(e) => setFilterStatus(e.target.value as TicketStatus | "All")}
                >
                    <option value="All">All Status</option>
                    {["Draft", "Submitted", "In Progress", "Resolved", "Rejected"].map((s) => (
                        <option key={s} value={s}>{s}</option>
                    ))}
                </select>
                <select
                    className="border rounded px-3 py-2"
                    value={filterPriority}
                    onChange={(e) => setFilterPriority(e.target.value as TicketPriority | "All")}
                >
                    <option value="All">All Priority</option>
                    {["Low", "Medium", "High"].map((p) => (
                        <option key={p} value={p}>{p}</option>
                    ))}
                </select>
            </div>

            {/* Table */}
            <div className="overflow-hidden rounded-xl border border-gray-200 bg-white shadow-sm">
                <div className="max-w-full overflow-x-auto">
                    <div className="min-w-[1102px]">
                        <Table>
                            <TableHeader className="bg-gray-50 border-b border-gray-100">
                                <TableRow>
                                    <TableCell isHeader className="px-5 py-3 text-left">ID</TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left">Subject</TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left">Category</TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left">Priority</TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left">Status</TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left">Created By</TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left">Created At</TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left">Attachments</TableCell>
                                </TableRow>
                            </TableHeader>

                            <TableBody className="divide-y divide-gray-100">
                                {displayedTickets.map((t) => (
                                    <TableRow key={t.id}>
                                        <TableCell className="px-5 py-4">{t.id}</TableCell>
                                        <TableCell>
                                            <span
                                                className="text-blue-600 cursor-pointer hover:underline"
                                                onClick={() => openTicket(t)}
                                            >
                                                {t.subject}
                                            </span>
                                        </TableCell>
                                        <TableCell>{t.category}</TableCell>
                                        <TableCell>
                                            <Badge
                                                size="sm"
                                                color={
                                                    t.priority === "High"
                                                        ? "error"
                                                        : t.priority === "Medium"
                                                            ? "warning"
                                                            : "success"
                                                }
                                            >
                                                {t.priority}
                                            </Badge>
                                        </TableCell>
                                        <TableCell>
                                            <Badge
                                                size="sm"
                                                color={
                                                    t.status === "Resolved"
                                                        ? "success"
                                                        : t.status === "Submitted" || t.status === "In Progress"
                                                            ? "warning"
                                                            : t.status === "Rejected"
                                                                ? "error"
                                                                : "light"
                                                }
                                            >
                                                {t.status}
                                            </Badge>
                                        </TableCell>
                                        <TableCell>{t.createdBy.name}</TableCell>
                                        <TableCell>{t.createdAt.split("T")[0]}</TableCell>
                                        <TableCell>
                                            {t.attachments.length > 0 ? (
                                                <ul>
                                                    {t.attachments.map((f, idx) => (
                                                        <li key={idx}>
                                                            <span
                                                                className="text-blue-600 underline cursor-pointer"
                                                                onClick={() => openAttachment(f)}
                                                            >
                                                                {f.name}
                                                            </span>
                                                        </li>
                                                    ))}
                                                </ul>
                                            ) : (
                                                <span className="text-gray-500 italic">No files</span>
                                            )}
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </div>
            </div>

            {/* Pagination */}
            {totalPages > 1 && (
                <div className="mt-4 flex justify-center gap-2">
                    <button
                        className="px-3 py-1 border rounded"
                        onClick={() => setCurrentPage(Math.max(currentPage - 1, 1))}
                        disabled={currentPage === 1}
                    >
                        Prev
                    </button>
                    {[...Array(totalPages)].map((_, idx) => (
                        <button
                            key={idx}
                            className={`px-3 py-1 border rounded ${currentPage === idx + 1 ? "bg-blue-600 text-white" : ""}`}
                            onClick={() => setCurrentPage(idx + 1)}
                        >
                            {idx + 1}
                        </button>
                    ))}
                    <button
                        className="px-3 py-1 border rounded"
                        onClick={() => setCurrentPage(Math.min(currentPage + 1, totalPages))}
                        disabled={currentPage === totalPages}
                    >
                        Next
                    </button>
                </div>
            )}

            {/* Modal */}
            <Modal isOpen={modalOpen} onClose={closeModal} className="max-w-lg p-6">
                <h2 className="text-lg font-semibold mb-4">{editingTicket ? "View/Edit Ticket" : "New Ticket"}</h2>

                <div className="space-y-4">
                    <div>
                        <label className="block text-sm font-medium mb-1">Subject</label>
                        <input
                            type="text"
                            className="w-full border rounded px-3 py-2"
                            value={subject}
                            onChange={(e) => setSubject(e.target.value)}
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">Description</label>
                        <textarea
                            className="w-full border rounded px-3 py-2"
                            value={description}
                            onChange={(e) => setDescription(e.target.value)}
                        />
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                        <div>
                            <label className="block text-sm font-medium mb-1">Category</label>
                            <select className="w-full border rounded px-3 py-2" value={category} onChange={(e) => setCategory(e.target.value as TicketCategory)}>
                                {["IT", "HR", "Facilities", "Other"].map((c) => (
                                    <option key={c} value={c}>{c}</option>
                                ))}
                            </select>
                        </div>
                        <div>
                            <label className="block text-sm font-medium mb-1">Priority</label>
                            <select className="w-full border rounded px-3 py-2" value={priority} onChange={(e) => setPriority(e.target.value as TicketPriority)}>
                                {["Low", "Medium", "High"].map((p) => (
                                    <option key={p} value={p}>{p}</option>
                                ))}
                            </select>
                        </div>
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">Attachments</label>
                        <input type="file" multiple className="w-full" onChange={handleAttachmentChange} />
                        {attachments.length > 0 ? (
                            <ul className="mt-2">
                                {attachments.map((f, idx) => (
                                    <li key={idx}>
                                        <span className="text-blue-600 underline cursor-pointer" onClick={() => openAttachment(f)}>{f.name}</span>
                                    </li>
                                ))}
                            </ul>
                        ) : (
                            <span className="text-gray-500 italic">No files attached</span>
                        )}
                    </div>

                    {/* Activity Stream */}
                    {editingTicket && (
                        <div className="border-t pt-3 mt-3">
                            <h3 className="font-medium text-gray-700 mb-2">Activity</h3>
                            <ul className="space-y-1">
                                {editingTicket.activity.map((act, idx) => (
                                    <li key={idx} className="flex justify-between">
                                        <span>{act.status}</span>
                                        <span className="text-gray-400 text-sm">{act.timestamp.split("T")[0]} {act.timestamp.split("T")[1]}</span>
                                    </li>
                                ))}
                            </ul>
                        </div>
                    )}

                    <div className="flex justify-end gap-2 mt-4">
                        <button className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400" onClick={closeModal}>Cancel</button>
                        <button className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700" onClick={handleSubmitTicket}>Submit</button>
                    </div>
                </div>
            </Modal>
        </div>
    );
}
